import React from 'react';
import logo from './logo.svg';
import './App.css';
import { Statslator } from 'statslator';

function App() {
  const groupVariables = [
    {name: 'Sample size', key: 'n'},
    {name: 'Sample mean', key: 'mean'},
    {name: 'Sample SD', key: 'SD'},
    {name: 'Sample SE', key: 'SE'},
  ]

  const otherVariables = [
    {name: 'Paired?', key: 'paired', type: 'boolean'},
    {name: 't-score', key: 't-score'},
    {name: 'p-value', key: 'p-value'},
    {name: 'Cohen\'s d', key: 'd'}
  ]

  const [knownVariables, setKnownVariables] = React.useState<{[name : string]: number}>({paired: 0})

  const onValueChanged = (key : string, value : string) => {
    if (value === '') {
      const newKnownVariables = {...knownVariables}
      delete newKnownVariables[key]
      setKnownVariables(newKnownVariables)
    } else {
      setKnownVariables({...knownVariables, [key]: parseFloat(value)})
    }
  }

  const calculatedValues = Statslator.calculate(knownVariables);

  console.log(knownVariables, calculatedValues)


  return (
    <div className="App">
      <header className="App-header">
        <div style={{ maxWidth: 800, margin: 'auto' }}>
          <div style={{width: '100%', display: 'flex', alignItems: 'center', flexDirection:'column'}}>
            <h1>Statslator</h1>
            <span style={{fontSize: 20}}>Translate between statistical values</span>
          </div>

          <h2 style={{ marginBottom: 3, marginTop: 60 }}>What you know</h2>
          Leave fields blank if you do not know the value


          <div style={{ display: 'flex', flexDirection: 'row', gap: 30, marginTop: 20 }}>
            <div style={{ display: 'flex', flexDirection: 'column', flexGrow: 1, maxWidth: 230, gap: 5, padding: 5 }}>
              <h3 style={{marginBottom: 10, marginTop: 0}}>Group 1</h3>

              {groupVariables.map((variable) => (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', width: '100%' }}>
                  <label>{variable.name}</label>
                  <input type="number" placeholder="Enter a number" style={{ width: 110 }}  onChange={(e) => onValueChanged(variable.key + "1", e.target.value)}/>
                </div>))}
            </div>

            <div style={{ display: 'flex', flexDirection: 'column', flexGrow: 1, maxWidth: 230, gap: 5, padding: 5 }}>
              <h3 style={{marginBottom: 10, marginTop: 0}}>Group 2</h3>

              {groupVariables.map((variable) => (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', width: '100%' }}>
                  <label>{variable.name}</label>
                  <input type="number" placeholder="Enter a number" style={{ width: 110 }}  onChange={(e) => onValueChanged(variable.key + "2", e.target.value)}/>
                </div>))}
            </div>

            <div style={{ display: 'flex', flexDirection: 'column', flexGrow: 1, maxWidth: 230, gap: 5, padding: 5 }}>
              <h3 style={{marginBottom: 10, marginTop: 0}}>Other</h3>

              {otherVariables.map((variable) => (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', width: '100%' }}>
                  <label>{variable.name}</label>
                  {variable.type === 'boolean' ? <input type="checkbox" style={{ width: 110 }}  onChange={(e) => onValueChanged(variable.key, e.target.checked ? "1" : "0")}/> : 
                  <input type="number" placeholder="Enter a number" style={{ width: 110 }}  onChange={(e) => onValueChanged(variable.key, e.target.value)}/>}
                </div>))}
            </div>
          </div>

          <h2>What can be calculated</h2>
        </div>
      </header>
    </div>
  );
}

export default App;
